/* global angular */
import getStyle from 'lib-bb-styles';
import { E_AUTH, E_CONNECTIVITY, E_UNEXPECTED, E_USER } from 'lib-bb-model-errors';
import composeSelectors from 'lib-wm-compose-selectors';
import {
  CSS_SELECTORS,
  POINT_RADIUS,
  POINT_HIT_RADIUS,
  LINE_TENSION,
  TOOLTIP_SHIFT,
  ARROW_WIDTH,
  MAX_EDGE_MULTIPLIER,
  TRANSPARENT,
  REM_TO_PIXEL_RATIO,
} from './constants';

/**
 * @typedef {Object} errorMessageDescriptor
 * @property {string} message Localization key of the error message
 * @property {string} title Localization key of the error message`s title
 * @property {string} icon CSS class for icon
 */

/**
 * @description
 * Map of error code to message, title and icon to display to user
 *
 * @public
 * @name errors
 * @type {object.<E_AUTH | E_CONNECTIVITY | E_UNEXPECTED | E_USER, errorMessageDescriptor>}
 */
const errors = {
  [E_AUTH]: {
    message: 'model.error.auth.message',
    title: 'model.error.auth.title',
    icon: 'fa-sign-in-alt',
  },
  [E_CONNECTIVITY]: {
    message: 'model.error.connectivity.message',
    title: 'model.error.connectivity.title',
    icon: 'fa-exclamation-triangle',
  },
  [E_USER]: {
    message: 'model.error.user.message',
    title: 'model.error.user.title',
    icon: 'fa-user',
  },
  [E_UNEXPECTED]: {
    message: 'model.error.unexpected.message',
    title: 'model.error.unexpected.title',
    icon: 'fa-server',
  },
};

/**
 * @description
 * Object that holds start date config for calendar component
 *
 * @private
 * @name startDateConfig
 * @type {object}
 */
let startDateConfig = {
  minDate: new Date(0),
  maxDate: new Date(),
  maxMode: 'year',
};

/**
 * @description
 * Object that holds end date config for calendar component
 *
 * @private
 * @name endDateConfig
 * @type {object}
 */
let endDateConfig = {
  minDate: new Date(0),
  maxDate: new Date(),
  maxMode: 'year',
};

/**
 * @description
 * Object that holds selected date range for calendar component
 *
 * @private
 * @name selectedDateRange
 * @type {object}
 */
const selectedDateRange = {
  dateFrom: null,
  dateTo: null,
};

const today = new Date();
const oneYearAgo = new Date().setFullYear(today.getFullYear() - 1);
const threeYearsAgo = new Date().setFullYear(today.getFullYear() - 3);
const allTime = new Date().setFullYear(today.getFullYear() - 5);

const startOfYear = new Date(today.getFullYear(), 0, 1);
const endOfYear = new Date(today.getFullYear(), 11, 31).setHours(23, 59, 59, 999);

const ticksPadding = parseFloat(getStyle('.p-3', 'padding')) * REM_TO_PIXEL_RATIO;

const GRANULARITY_DAILY = 'daily';
const GRANULARITY_WEEKLY = 'weekly';
const GRANULARITY_MONTHLY = 'monthly';
const GRANULARITY_QUARTERLY = 'quarterly';

/**
 * @description
 * Map of granularity to date formats
 *
 * @private
 * @name GRANULARITY_TO_DATE_FORMAT
 * @type {object.<GRANULARITY_DAILY
 * | GRANULARITY_WEEKLY
 * | GRANULARITY_MONTHLY
 * | GRANULARITY_QUARTERLY, number>}
 */
const GRANULARITY_TO_DATE_FORMAT = {
  [GRANULARITY_DAILY]: 'mediumDate',
  [GRANULARITY_WEEKLY]: 'mediumDate',
  [GRANULARITY_MONTHLY]: 'MMM y',
  [GRANULARITY_QUARTERLY]: 'MMM y',
};

/**
 * @description
 * Set of available periods for chart
 *
 * @public
 * @name periods
 * @type {array}
 */
const periods = [
  {
    i18nKey: 'performanceVariation.ytd',
    active: true,
    granularity: GRANULARITY_MONTHLY,
    value: { dateTo: endOfYear, dateFrom: startOfYear },
  },
  {
    i18nKey: 'performanceVariation.year',
    value: { dateTo: today, dateFrom: oneYearAgo },
    granularity: GRANULARITY_MONTHLY,
  },
  {
    i18nKey: 'performanceVariation.3years',
    value: { dateTo: today, dateFrom: threeYearsAgo },
    granularity: GRANULARITY_QUARTERLY,
  },
  {
    i18nKey: 'performanceVariation.all',
    granularity: GRANULARITY_QUARTERLY,
    value: { dateTo: today, dateFrom: allTime },
  },
];

const helpers = ({ $filter, stateContainer }) => {
  const dateFilter = $filter('date');
  const numberFilter = $filter('number');

  let chartInstance;
  let $ctrl = null;
  let debounceTimeout = null;

  const valuationChartBarColorPositive
    = getStyle(CSS_SELECTORS.valuationChartBarPositive, 'color');
  const valuationChartBarColorNegative
    = getStyle(CSS_SELECTORS.valuationChartBarNegative, 'color');
  const performanceChartPrimaryColor
    = getStyle(CSS_SELECTORS.performanceChartPrimary, 'color');
  const hoverColor = getStyle('.btn-outline-secondary', 'color');

  const memoCtrl = (ctrl) => {
    $ctrl = ctrl;
  };

  /**
   * @description
   * Maps colours to bars
   *
   * @private
   * @param {array} data Set of bar data
   * @type {function}
   * @returns {array}
   */
  const getBarColor = data => data.map(item => {
    if (item.valuePct > 0) { return valuationChartBarColorPositive; }
    return valuationChartBarColorNegative;
  });

  /**
   * @description
   * Holds options of chart's plugins
   *
   * @name chartPlugins
   * @type {object}
   */
  const chartPlugins = {
    afterInit(chart) {
      chartInstance = chart;
    },
  };

  /**
   * @description
   * Shows value as it is or adds two decimal zeroes if needed
   *
   * @name formatAmount
   * @type {function}
   * @param {number} value Value to format
   * @returns {number} Formatted value
   */
  const formatAmount = (value) => {
    if (value === 0) { return value; }
    if (!value) { return ''; }
    const decimalsLength = (value.toString().split('.')[1] || []).length;
    const fractionSize = decimalsLength < 2 ? 2 : decimalsLength;
    return numberFilter(value, fractionSize);
  };

  /**
   * @description
   * Retrieves selected granularity from stateContainer
   *
   * @public
   * @name getGranularity
   * @type {function}
   *
   * @returns {GRANULARITY_DAILY
   * | GRANULARITY_WEEKLY
   * | GRANULARITY_MONTHLY
   * | GRANULARITY_QUARTERLY} selected granularity
   */
  const getGranularity = () => (
    stateContainer.getState().portfolioPerformance.granularity
  );

  /**
   * @description
   * Method to make charts` ticks more rare
   *
   * @private
   * @name ticksCallback
   * @type {function}
   * @param {object} tick Chart tick
   * @param {index} index Index of the tick in the array of all ticks
   * @param {object} ticks Array of all ticks
   */
  const ticksCallback = (tick, index, data) => {
    let result = null;
    const granularity = getGranularity();

    if (granularity === GRANULARITY_DAILY || granularity === GRANULARITY_WEEKLY) {
      const lastItem = index === data.length - 1;
      result = (index % 2 === 0 || lastItem) && tick;
    } else {
      result = tick;
    }

    return result ? dateFilter(result, GRANULARITY_TO_DATE_FORMAT[granularity]) : null;
  };

  /**
   * @description
   * Configuration for variation chart`s Y axis
   *
   * @private
   * @name variationYAxisConfig
   * @type {function}
   * @param {number} edge Edge of chart's values
   * @returns {object} Configuration for variation chart`s Y axis
   */
  const variationYAxisConfig = edge => ({
    ticks: {
      callback(label) {
        return `${Math.round(label * 10) / 10}%`;
      },
      stepSize: edge / 5,
      beginAtZero: true,
      padding: ticksPadding,
      suggestedMin: -edge,
      suggestedMax: edge,
    },
    type: 'linear',
    display: true,
    position: 'left',
    id: 'y-axis-1',
    gridLines: {
      display: true,
      drawOnChartArea: true,
      drawBorder: false,
    },
  });

  /**
   * @description
   * Configuration for performance chart`s Y axis
   *
   * @private
   * @name performanceYAxisConfig
   * @type {function}
   * @param {number} edge Edge of chart's values
   * @returns {object} Configuration for performance chart`s Y axis
   */
  const performanceYAxisConfig = edge => ({
    ticks: {
      callback(label) {
        return `${Math.round(label * 10) / 10}%`;
      },
      stepSize: edge / 5,
      fontColor: performanceChartPrimaryColor,
      padding: ticksPadding,
      suggestedMin: -edge,
      suggestedMax: edge,
    },
    type: 'linear',
    display: true,
    position: 'right',
    id: 'y-axis-2',
    gridLines: {
      color: TRANSPARENT,
      display: false,
      drawBorder: false,
    },
  });

  /**
   * @description
   * Sets styling and content for custom tooltip
   *
   * @name getCustomTooltip
   * @type {function}
   * @param {object} state State of widget
   * @param {object} data Chartjs model of selected item
   * @returns {void}
   */
  const getCustomTooltip = dataLength => data => {
    // eslint-disable-next-line no-undef
    const tooltipEl = document
      .querySelector('[data-role=performance-variation-spiderweb-chart-tooltip]')
      .querySelector('.popover');
    const arrow = tooltipEl.querySelector('.arrow');

    if (data.opacity === 0) {
      tooltipEl.classList.remove('show');
      return;
    }

    if (!data.dataPoints[1]) { return; }

    const { index } = data.dataPoints[1];
    const variationValue = data.dataPoints[1].yLabel;
    const variationDate = data.dataPoints[1].xLabel;
    const performanceValue = data.dataPoints[0].yLabel;

    $ctrl.setActiveItem(index);

    const titleWrapper = tooltipEl.querySelector('.popover-title-content');
    titleWrapper.innerHTML = variationDate;

    const xAlign = index > dataLength / 2 ? 'right' : 'left';

    tooltipEl.classList.remove('bs-popover-right');
    tooltipEl.classList.remove('bs-popover-left');
    tooltipEl.classList.add(`bs-popover-${xAlign === 'left' ? 'right' : 'left'}`);

    const performanceWrapper = tooltipEl.querySelector('.popover-performance');
    const performanceSign = performanceValue > 0 ? '+' : '';
    performanceWrapper.innerHTML = `${performanceSign}${formatAmount(performanceValue)}`;

    const performanceDotWrapper = tooltipEl.querySelector('.popover-performance-dot');
    performanceDotWrapper.style.color = performanceChartPrimaryColor;

    const variationWrapper = tooltipEl.querySelector('.popover-variation');
    const variationSign = variationValue > 0 ? '+' : '';
    variationWrapper.innerHTML = `${variationSign}${formatAmount(variationValue)}`;

    const variationDotWrapper = tooltipEl.querySelector('.popover-variation-dot');
    if (variationValue < 0) {
      variationDotWrapper.style.color = valuationChartBarColorNegative;
    } else {
      variationDotWrapper.style.color = valuationChartBarColorPositive;
    }

    let left = data.caretX + TOOLTIP_SHIFT;
    if (xAlign === 'right') {
      left -= tooltipEl.offsetWidth + ARROW_WIDTH;
    } else {
      left += ARROW_WIDTH;
    }

    Object.assign(tooltipEl.style, {
      'pointer-events': 'none',
      top: `${(chartInstance.scales['y-axis-1'].maxHeight / 2) +
            + (tooltipEl.offsetHeight - arrow.offsetHeight) + 3}px`,
      left: `${left}px`,
    });
    Object.assign(arrow.style, {
      top: `${(tooltipEl.offsetHeight / 2) - (arrow.offsetHeight / 2)}px`,
    });
    tooltipEl.classList.add('show');
  };

  /**
   * @description
   * Calculates suggested edge for chart
   *
   * @private
   * @name getVariationSuggestedEdge
   * @type {function}
   * @param {array} chartData Array of chart's items
   * @returns {number} Suggested edge for chart
   */
  const getVariationSuggestedEdge = (chartData) => {
    const variationMin = Math.min(...chartData.map(item => item.valuePct));
    const variationMax = Math.max(...chartData.map(item => item.valuePct));
    const variationEdge = Math.max(Math.abs(variationMin), Math.abs(variationMax));
    return variationEdge * MAX_EDGE_MULTIPLIER;
  };

  /**
   * @description
   * Calculates suggested edge for chart
   *
   * @private
   * @name getPerformanceSuggestedEdge
   * @type {function}
   * @param {array} chartData Array of chart's items
   * @returns {number} Suggested edge for chart
   */
  const getPerformanceSuggestedEdge = (chartData) => {
    const performancemMin = Math.min(...chartData.map(item => item.value));
    const performanceMax = Math.max(...chartData.map(item => item.value));
    const performanceEdge = Math.max(Math.abs(performancemMin), Math.abs(performanceMax));
    return performanceEdge * MAX_EDGE_MULTIPLIER;
  };

  /**
   * @description
   * Configuration of variation chart
   *
   * @public
   * @name chartVariationOptions
   * @type {function}
   * @param {object} state State of widget
   * @returns {object} Configuration of variation chart
   */
  const chartVariationOptions = (dataAvailable, performanceData, valuationData) => {
    if (!dataAvailable) { return null; }

    const variationSuggestedEdge = getVariationSuggestedEdge(valuationData.chartData);
    const performanceSuggestedEdge = getPerformanceSuggestedEdge(performanceData.chartData);

    return ({
      animation: false,
      tooltips: {
        enabled: false,
        mode: 'index',
        axis: 'x',
        position: 'average',
        intersect: false,
        custom: getCustomTooltip(valuationData.chartData.length),
      },
      hover: {
        mode: null,
      },
      scales: {
        yAxes: [
          variationYAxisConfig(variationSuggestedEdge),
          performanceYAxisConfig(performanceSuggestedEdge),
        ],
        xAxes: [
          {
            ticks: {
              callback: ticksCallback,
            },
          },
        ],
      },
    });
  };

  /**
   * @description
   * Returns error object by error code
   *
   * @public
   * @name getError
   * @type {function}
   * @returns {errorMessageDescriptor} Descriptor of the error
   */
  const getError = (code) => errors[code];

  /**
   * @description
   * Produces labels out of given array
   *
   * @private
   * @name getLabels
   * @type {function}
   * @param {array} data Set of data to produce labels
   * @returns {number[]} Set of numeric labels
   */
  const getLabels = data => {
    const lastItem = data[data.length - 1];

    return data
      .concat(
        [...Array(data.length < 12 && periods[0].active === true ? 12 - data.length : 0)]
          .map((number, index) => ({
            dateFrom: new Date(lastItem.dateFrom)
              .setMonth(new Date(lastItem.dateFrom).getMonth() + index + 1),
            dateTo: new Date(lastItem.dateTo)
              .setMonth(new Date(lastItem.dateTo).getMonth() + index + 1),
          })))
      .map(item => (new Date(item.dateFrom).getTime() + new Date(item.dateTo).getTime()) / 2);
  };

  /**
   * @description
   * Creates dataset for chart out of variation data
   *
   * @private
   * @name getVariationDataset
   * @param {array} data List of portfolio variations
   * @returns {object} Variation dataset for chartjs library
   */
  const getVariationDataset = data => ({
    label: '',
    backgroundColor: getBarColor(data),
    borderWidth: 0,
    yAxisID: 'y-axis-1',
    data: data.map(item => item.valuePct),
  });

  /**
   * @description
   * Creates dataset for chart out of performance data
   *
   * @private
   * @name getPerformanceDataset
   * @param {array} data List of portfolio performance
   * @returns {object} Performance dataset for chartjs library
   */
  const getPerformanceDataset = data => ({
    type: 'line',
    label: '',
    borderWidth: 0,
    yAxisID: 'y-axis-2',
    data: data.map(item => ({
      x: (new Date(item.dateFrom).getTime() + new Date(item.dateTo).getTime()) / 2,
      y: item.value,
    })),
    backgroundColor: performanceChartPrimaryColor,
    borderColor: performanceChartPrimaryColor,
    pointRadius: POINT_RADIUS,
    pointHitRadius: POINT_HIT_RADIUS,
    lineTension: LINE_TENSION,
    fill: false,
  });

  /**
   * @name portfolioPerformanceDataSelector
   * @inner
   * @description Selector for getting performance data from state
   * @type function
   * @returns {Array} Performance data
   */
  const portfolioPerformanceDataSelector = stateContainer.createSelector(
    state => state.portfolioPerformance && state.portfolioPerformance.data
  );

  /**
   * @name portfolioValuationDataSelector
   * @inner
   * @description Selector for getting valuation data from state
   * @type function
   * @returns {Array} Valuation data
   */
  const portfolioValuationDataSelector = stateContainer.createSelector(
    state => state.valuation && state.valuation.data
  );

  /**
   * @name activeItemSelector
   * @inner
   * @description Selector for getting active item from state
   * @type function
   * @returns {Object} Active item
   */
  const activeItemSelector = stateContainer.createSelector(
    state => state.activeItem
  );

  /**
   * @description
   * Tells whether extension retrieved data
   *
   * @public
   * @name isDataAvailable
   * @type {function}
   * @returns {boolean} Flag that tells whether data is available
   */
  const isDataAvailable = composeSelectors(
    portfolioPerformanceDataSelector,
    portfolioValuationDataSelector,
    (performanceData, valuationData) => {
      if (
        !performanceData ||
        !performanceData.chartData ||
        performanceData.chartData.length === 0
      ) { return false; }
      if (
        !valuationData ||
        !valuationData.chartData ||
        valuationData.chartData.length === 0
      ) { return false; }

      return true;
    });

  /**
   * @description
   * Selector which retrieves perfromance data out of store
   * and converts it into chartjs suitable format
   *
   * @public
   * @name getPerformanceVariationSeries
   * @type {function}
   * @returns {object} Series object of performance data for chartjs library
   */
  const getPerformanceVariationSeries = composeSelectors(
    isDataAvailable,
    portfolioPerformanceDataSelector,
    portfolioValuationDataSelector,
    (dataAvailable, performanceData, valuationData) => {
      if (!dataAvailable) {
        return { labels: [], datasets: [] };
      }

      return {
        labels: getLabels(valuationData.chartData),
        datasets: [
          getPerformanceDataset(performanceData.chartData),
          getVariationDataset(valuationData.chartData),
        ],
        updated: true,
      };
    });

  /**
   * @description
   * Gets options for variation chart
   *
   * @public
   * @name getVariationOptions
   * @type {function}
   * @returns {object} Variation chart options
   */
  const getVariationOptions = composeSelectors(
    isDataAvailable,
    portfolioPerformanceDataSelector,
    portfolioValuationDataSelector,
    chartVariationOptions
  );

  /**
   * @description
   * Makes active flag falsy for all periods
   *
   * @private
   * @name resetActivePeriod
   * @type {function}
   * @returns {void}
   */
  const resetActivePeriod = () => {
    periods.forEach(period => Object.assign(period, { active: false }));
  };

  /**
   * @description
   * Resets selected date range for calendar
   *
   * @private
   * @name resetDatePickerPeriod
   * @type {function}
   * @returns {void}
   */
  const resetDatePickerPeriod = () => {
    selectedDateRange.dateFrom = null;
    selectedDateRange.dateTo = null;
  };

  /**
   * @description
   * Sets active period of time by given index of selected period
   *
   * @public
   * @name setActivePeriod
   * @type {function}
   * @param {number} index Index of selected period
   * @returns {void}
   */
  const setActivePeriod = index => {
    const currentActivePeriodIndex = periods.findIndex(period => period.active);
    if (currentActivePeriodIndex !== -1) {
      periods[currentActivePeriodIndex].active = false;
    }
    periods[index].active = true;
    resetDatePickerPeriod();
  };

  /**
   * @description
   * Fires setting of period over a period of time
   *
   * @private
   * @name debounceSettingPeriod
   * @type {function}
   * @returns {void}
   */
  const debounceSettingPeriod = () => {
    if (debounceTimeout) {
      clearTimeout(debounceTimeout);
    }
    debounceTimeout = setTimeout(() => {
      resetActivePeriod();
      $ctrl.setPeriod({ granularity: GRANULARITY_MONTHLY, value: selectedDateRange });
    });
  };

  /**
   * @description
   * Object, that holds start and end dates for datepicker
   *
   * @private
   * @name datePickerRange
   * @type {object}
   */
  const datePickerRange = {
    set startDate(v) {
      selectedDateRange.dateFrom = v;
      endDateConfig = {
        ...endDateConfig,
        minDate: new Date(v),
      };

      if (selectedDateRange.dateTo) {
        debounceSettingPeriod();
      }
    },
    get startDate() {
      return selectedDateRange.dateFrom;
    },
    set endDate(v) {
      selectedDateRange.dateTo = v;
      startDateConfig = {
        ...startDateConfig,
        maxDate: new Date(v),
      };

      if (selectedDateRange.dateFrom) {
        debounceSettingPeriod();
      }
    },
    get endDate() {
      return selectedDateRange.dateTo;
    },
  };

  /**
   * @description
   * Gets range of datepicker
   *
   * @public
   * @name getDatePickerRange
   * @type {function}
   * @returns {object} Date picker range
   */
  const getDatePickerRange = () => datePickerRange;

  /**
   * @description Gets start date config object
   *
   * @public
   * @name getStartDateConfig
   * @type {function}
   * @returns {object} Start date config
   */
  const getStartDateConfig = () => startDateConfig;

  /**
   * @description Gets end date config object
   *
   * @public
   * @name getEndDateConfig
   * @type {function}
   * @returns {object} End date config
   */
  const getEndDateConfig = () => endDateConfig;

  /**
   * @description
   * Configuration for variation hover chart`s Y axis
   *
   * @private
   * @name variationHoverYAxisConfig
   * @type {function}
   * @param {number} edge Edge of chart's values
   * @returns {object} Configuration for variation hover chart`s Y axis
   */
  const variationHoverYAxisConfig = (edge) => ({
    ticks: {
      callback(label, index) {
        return `${Math.round((edge - ((edge / 5) * index)) * 10) / 10}%`;
      },
      stepSize: 0.1,
      fontColor: TRANSPARENT,
      beginAtZero: true,
      padding: ticksPadding,
      min: 0,
      max: 1,
    },
    type: 'linear',
    display: true,
    position: 'left',
    id: 'y-axis-3',
    gridLines: {
      display: true,
      color: TRANSPARENT,
      drawOnChartArea: true,
      drawBorder: false,
    },
  });

  /**
   * @description
   * Configuration for performance hover chart`s Y axis
   *
   * @private
   * @name performanceHoverYAxisConfig
   * @type {function}
   * @param {number} edge Edge of chart's values
   * @returns {object} Configuration for performance hover chart`s Y axis
   */
  const performanceHoverYAxisConfig = (edge) => ({
    ticks: {
      callback(label, index) {
        return `${Math.round((edge - ((edge / 5) * index)) * 10) / 10}%`;
      },
      stepSize: 0.1,
      fontColor: TRANSPARENT,
      padding: ticksPadding,
      min: 0,
      max: 1,
    },
    type: 'linear',
    display: true,
    position: 'right',
    id: 'y-axis-4',
    gridLines: {
      color: TRANSPARENT,
      display: false,
      drawBorder: false,
    },
  });

  /**
   * @description
   * Selector which retrieves perfromance data out of store
   * and converts it into chartjs suitable format
   *
   * @public
   * @name getHoverSeries
   * @type {function}
   * @returns {object} Series object of performance data for hover area
   */
  const getHoverSeries = composeSelectors(
    isDataAvailable,
    portfolioPerformanceDataSelector,
    portfolioValuationDataSelector,
    activeItemSelector,
    (dataAvailable, performanceData, valuationData, activeItem) => {
      if (!dataAvailable) {
        return { labels: [], datasets: [] };
      }

      return {
        labels: getLabels(valuationData.chartData),
        datasets: [
          {
            label: '',
            data: performanceData.chartData.map(() => 1),
            backgroundColor: performanceData.chartData.map((item, index) => (
              index === activeItem ? hoverColor : TRANSPARENT
            )),
            borderWidth: 0,
            yAxisID: 'y-axis-3',
          },
        ],
        updated: true,
      };
    });

  /**
   * @description
   * Configuration of hover chart area
   *
   * @public
   * @name getHoverOptions
   * @type {function}
   * @param {object} state State of widget
   * @returns {object} Configuration of hover chart area
   */
  const getHoverOptions = composeSelectors(
    isDataAvailable,
    portfolioPerformanceDataSelector,
    portfolioValuationDataSelector,
    (dataAvailable, portfolioPerformanceData, valuationData) => {
      if (!dataAvailable) { return null; }

      const variationSuggestedEdge = getVariationSuggestedEdge(valuationData.chartData);
      const performanceSuggestedEdge =
        getPerformanceSuggestedEdge(portfolioPerformanceData.chartData);

      return {
        animation: false,
        scales: {
          yAxes: [
            variationHoverYAxisConfig(variationSuggestedEdge),
            performanceHoverYAxisConfig(performanceSuggestedEdge),
          ],
          xAxes: [
            {
              ticks: {
                callback: ticksCallback,
                fontColor: TRANSPARENT,
              },
              gridLines: {
                display: false,
                color: TRANSPARENT,
              },
            },
          ],
        },
      };
    });

  return {
    getError,
    periods,
    getVariationOptions,
    getPerformanceVariationSeries,
    setActivePeriod,
    getDatePickerRange,
    chartPlugins,
    memoCtrl,
    getStartDateConfig,
    getEndDateConfig,
    getHoverSeries,
    getHoverOptions,
    isDataAvailable,
  };
};

export default helpers;



